<template>
	<div id="single-blog">
		<h1>{{blog.title}}</h1>
		<article>{{blog.content}}</article>
		<p>分类：<span v-for="category in blog.categories">{{category}}&nbsp;&nbsp;&nbsp;</span></p>
		<p>作者：{{blog.author}}</p>
		<button @click="deleblog()">删除</button>
  		<router-link tag="button" :to="'/edit/'+ id">编辑</router-link>
	</div>
</template>

<script>
	import axios from 'axios'
	export default{
		name:"single-blog",
		data(){
			return{
				id:this.$route.params.id,
				blog:{}
			}
		},
		created(){
//			this.$http.get('https://blog-46a55.firebaseio.com/posts/'+this.id + '.json').then(function(data){
			axios.get('https://wd9381553209wlvukz.wilddogio.com/posts/'+this.id + '.json').then(function(data){
//				console.log(data);
//				this.blog = data.body;
				return data.data;
			}).then((data)=>{
				this.blog = data;
			})
		},
		methods:{
		  	deleblog(){
//			  	this.$http.delete('https://blog-46a55.firebaseio.com/posts/'+this.id + '.json').then(function(){
			  	axios.delete('https://wd9381553209wlvukz.wilddogio.com/posts/'+this.id + '.json').then(()=>{
			  		this.$router.push({path:'/'})
			  	})
		  	}
		},
	}
</script>

<style>
	#single-blog{
		max-width: 960px;
		margin:0 auto;
		padding: 20px;
		background: #eee;
		border: 1px dotted #aaa;
	}
</style>


